/* eslint-disable react/jsx-no-target-blank */
/* eslint-disable array-callback-return */
/* eslint-disable eqeqeq */
import React, { useState, useEffect } from "react";
import fetch from "node-fetch";
import "./App.css";

export default function App(){

  const [data, setData] = useState([]);
  const [search, setSearch] = useState("");
  const [length, setLength] = useState(0);
  const [load, setLoad] = useState(10);

  useEffect(() => {
    fetch(`https://github.com/akikungz/raw-file-host/raw/main/data.json`).then(res => res.json()).then(res => {
      console.log(res);
      setData(res.data);
      setLength(res.data.length);
    }).catch(console.error);
  }, []);

  function loadBtn(){
    if(length > 0){
      if(length > load){
        return <button 
          className="btn btn-primary shadow-sm" 
          style={{width: "100%"}}
          onClick={() => setLoad(load + 10)}
        >Load more...</button>
      }else {
        return undefined;
      }
    }
    return undefined;
  }

  return (
    <div className="container">
      <h4>เฉลย Virtual School ม.6</h4>
      <input type="text" id="key" className="form-control shadow-sm" onChange={(e) => {
          setSearch(e.target.value.toLowerCase());
          setLength(data.filter(value => {
            if(e.target.value == "") return value;
            if(value.key.toLowerCase().includes(e.target.value.toLowerCase())){
              return value;
            }
          }).length);
          setLoad(10);
        }} placeholder="Key..." />
        <span>All result {length} data</span>
        <table className="table table-hover table-bordered table-dark">
          <thead className="table-secondary sticky-custom">
            <tr>
              <th scope="col">Key</th>
              <th scope="col">Value</th>
            </tr>
          </thead>
          <tbody>
            {
              data.filter((value, i) => {
                if(search == "") return value;
                if(value.key.toLowerCase().includes(search)){
                  return value;
                }
              }).map((res, i) => {
                if(i <= load) return <tr><td>{res.key}</td><td>{res.value}</td></tr>
              })
            }
            { !length ? <tr><td colSpan={2}>Undefined</td></tr> : undefined }
          </tbody>
        </table>
        { loadBtn() }
    </div>
  )
}